.nav-header {
  background-color: $white;
  padding: 0 0 10px;
  .container {
    margin-left: auto;
    margin-right: auto;
    padding: 10px 15px 0; } }

.nav-menu {
  float: right;
  margin: 5px 0 0;
  .nav-menu-list {
    font-size: $font-large;
    list-style: none;
    margin: 0;
    padding: 0;
    .nav-item {
      display: inline-block;
      margin: 0;
      vertical-align: top;
      .nav-a {
        border: 1px solid transparent;
        color: $base-dark;
        display: block;
        padding: 5px 10px;
        text-decoration: none;
        text-transform: uppercase;
        &:hover {
          color: $red; } } } } }
