.btn {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  font-weight: $font-weight-normal;
  font-size: $font-base;
  line-height: $base-line-height;
  border: $border-width solid transparent;
  border-radius: $border-radius;
  transition: $btn-transition;
  @include btn-sizing($btn-padding-y, $btn-padding-x, $font-base, $border-radius); }

.buy-btn-lg {
  @include btn-sizing(12px, 0, $font-large, 30px);
  width: 100%; }

.buy-btn-md {
  @include btn-sizing(18px, 50px, $font-base, 30px); }

.btn-dark {
  @include btn-variant($btn-main-color, lighten($btn-dark-bg, 40%), $btn-border-dark); }

.btn-primary {
  @include btn-variant($btn-main-color, $btn-primary-bg, $btn-border-primary); }

.btn-third {
  @include btn-variant($btn-main-color, $btn-third-bg, $btn-border-third); }
